import React from 'react';
import  Chart from 'react-apexcharts';
import { Header } from '../Header';
import { Box } from '@material-ui/core';

const option = {
  chart: {
    zoom: {
      enabled: true,
      autoScaleYaxis: true,
    },
  },
  dataLabels: {
    enabled: false
  },
  grid: {
    row: {
      colors: ['#f3f3f3', 'transparent'],
      opacity: 0.5
    },
    clipMarkers: false
  },
  toolbar: {
    tools: {
      selection: false
    }
  },
  tooltip: {
    followCursor: false,
    theme: 'dark',
    x: {
      show: false
    },
    marker: {
      show: false
    },
    y: {
      title: {
        formatter: function () {
          return ''
        }
      }
    }
  },
  yaxis: {
    show: true,
    showAlways: true,
    tickAmount: 4,
    labels: {
      show: true,
      maxWidth: 50,
      formatter: (value) => { return value.toFixed(2) },
    },
    axisBorder: {
        show: true,
        color: '#78909C',
        offsetX: 0,
        offsetY: 0
    },
    title: {
      text: 'YAxis'
    }
  },
  xaxis: {
    type: 'datetime',
    labels: {
      datetimeFormatter: {
        year: 'yyyy',
        month: 'MM-dd',
        day: 'MM-dd',
        hour: 'HH:mm'
      }
    }
  }
}

 export const LineChart = ({ series = [], options={}, events, panel, type = 'line' }) => {

  return (
    <Box>
      <Header
        variant="subtitle2"
        title="title"
        boxProps={{
          flex: '0 0 auto'
        }}
        events={events}
        panel={panel}
      />
        <Box height={1}>
          <Chart options={{...option, ...options}} series={series} type={type} />
        </Box>
    </Box>
  );
}
