import React, { useState } from 'react';
import {
  Box,
  Typography,
  makeStyles,
  createStyles,
  Button
} from '@material-ui/core';
import cn from 'classnames';
import Icon from '@mdi/react';
import { mdiArrowExpand, mdiArrowCollapse } from '@mdi/js';

const useStyles = makeStyles(theme =>
  createStyles({
    box: {
      height: '30px',
      borderBottom: `1px solid #000`,
      cursor: 'move',
      background: 'skyblue'
    },
    zoomInIcon: {
      cursor: 'zoom-in'
    },
    zoomOutIcon: {
      cursor: 'zoom-out'
    },
    button: {
      padding: 0,
      minWidth: 'auto'
    }
  })
);

export const Header = ({
  title,
  events,
  panel,
  variant,
  boxProps,
  ...typographyProps
}) => {
  const classes = useStyles();
  const [isExpanded, setIsExpanded] = useState(false);
  const handleExpandClick = () => {
    events.expandHandle(panel, isExpanded);
    setIsExpanded(!isExpanded);
  };
  return (
    <Box
      px={1}
      display="flex"
      alignItems="center"
      justifyContent="space-between"
      data-testid="header-wrapper-box"
      className={cn(classes.box, 'dragHandle')}
      {...boxProps}
    >
      <Typography variant={variant} color="textPrimary" {...typographyProps}>
        {title}
      </Typography>
      <Button className={classes.button} onClick={handleExpandClick}>
        {isExpanded ? (
          <Icon
            path={mdiArrowCollapse}
            size={0.6}
            className={classes.zoomOutIcon}
            color="rgba(255, 255, 255, 0.6)"
          />
        ) : (
          <Icon
            path={mdiArrowExpand}
            size={0.6}
            className={classes.zoomInIcon}
            color="rgba(255, 255, 255, 0.6)"
          />
        )}
      </Button>
    </Box>
  );
};
