import React, { useEffect, useState } from 'react';
import { LineChart } from '../LineChart';
import { HeatmapChart } from '../HeatMapChart';
import axios from 'axios';
import { Box, Typography, useTheme } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { WidthProvider, Responsive } from 'react-grid-layout';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import 'react-grid-layout/css/styles.css';

const ResponsiveReactGridLayout = WidthProvider(Responsive);
const useStyles = makeStyles({
  card: {
    border: '1px solid #eee'
  }
});

const Dashboard = () => {
  const classes = useStyles();
  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('xs'));
  const [ohlc, setOhlc] = useState([]);
  const [volume, setVolume] = useState([]);
  const [list2, setList2] = useState([]);
  const [heatmapData, setHeatmapData] = useState([]);
  useEffect(()=>{
    const http = async () => {
      const list = [];
      const list2 = [];
      const vol = [];
      const result = await axios.get('https://data.jianshukeji.com/stock/history/000001')
        .then(function (response) {
          return response.data
        })
        .catch(function (error) {
          console.log(error);
        });

      if(result && result.data.length > 0){
        for (let i = 0; i < result.data.length; i += 1) {
            list.push([
                result.data[i][0], // the date
                result.data[i][1], // open
                result.data[i][2], // high
                result.data[i][3], // low
                result.data[i][4] // close
            ]);
            vol.push([
                result.data[i][0], // the date
                result.data[i][5] // the volume
            ]);

            list2.push([
              result.data[i][0], // the date
              result.data[i][2] - 2, // high
            ])

        }
        setOhlc(list);
        setVolume(vol);
        setList2(list2)
      }
    }
    const getHeatMapData = async () => {
      const result = await axios.get('https://raw.githubusercontent.com/markgeek/trading-data/master/retracement_margin_value.json.json')
        .then(function (response) {
          return response.data
        })
        .catch(function (error) {
          console.log(error);
          return {}
        });
      const data = Object.keys(result).map(y=>{
          const value = Object.keys(result[y]).map(x => {
            return {x:x, y: result[y][x]}
          })
          return {
            name: y,
            data: value
          }
      })
      setHeatmapData(data)
    }
    http();
    getHeatMapData();
  }, [])

  const defaultLayouts = {
    md: [
      { i: '1', x: 0, y: 0, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '2', x: 4, y: 0, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '3', x: 8, y: 0, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '4', x: 0, y: 2, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '5', x: 4, y: 2, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '6', x: 8, y: 2, w: 4, h: 2, minW: 2, minH: 2 }
    ],
    xs: [
      { i: '1', x: 0, y: 0, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '2', x: 0, y: 4, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '3', x: 0, y: 8, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '4', x: 0, y: 12, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '5', x: 0, y: 16, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '6', x: 0, y: 20, w: 4, h: 2, minW: 2, minH: 2 }
    ],
    xxs: [
      { i: '1', x: 0, y: 0, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '2', x: 0, y: 4, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '3', x: 0, y: 8, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '4', x: 0, y: 12, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '5', x: 0, y: 16, w: 4, h: 2, minW: 2, minH: 2 },
      { i: '6', x: 0, y: 20, w: 4, h: 2, minW: 2, minH: 2 }
    ]
  };
  const getExpandedLayouts = (key) => {
    return {
      md: [
        {
          i: key,
          x: 0,
          y: 0,
          w: 12,
          h: 6
        }
      ],
      xs: [
        {
          i: key,
          x: 0,
          y: 0,
          w: 4,
          h: 20
        }
      ],
      xxs: [
        {
          i: key,
          x: 0,
          y: 0,
          w: 2,
          h: 20
        }
      ]
    };
  };
  const getCurrentLayouts = () => {
    return { ...defaultLayouts, ...getLayoutsFromLS() };
  };
  const [layouts, setLayouts] = useState(getCurrentLayouts());

  const onLayoutChange = (currentLayout, allLayouts) => {
    if (allLayouts.md.length === 1) return; // expand one panel, don't save the layouts
    saveLayoutsToLS(allLayouts);
  };

  const events = {
    expandHandle(key, isExpanded) {
      if (!isExpanded) {
        setLayouts(getExpandedLayouts(key));
      } else {
        setLayouts(getCurrentLayouts());
      }
    }
  };

  const line1 = [
    {
      name: 'long',
      data: list2
    },
    {
      name: 'short',
      data: ohlc
    }
  ]
  const line2 = [
    {
      name: 'long',
      data: volume
    }
  ]

    return (
      <Box display='flex' flexDirection='column' p={2}>
      <Box display='flex' justifyContent= 'space-around' mb={2}>
        <Box width='15%' p={1} className={classes.card}>
          <Typography variant="body1">
            最大回撤
          </Typography>
          <Typography variant="body1">
            美元:
            <span>19999999</span>
          </Typography>
          <Typography variant="body1">
            币:
            <span>19999999</span>
          </Typography>
        </Box>
        <Box width='15%' p={1} className={classes.card}>
          <Typography variant="body1">
            最大回撤
          </Typography>
          <Typography variant="body1">
            美元
          </Typography>
          <Typography variant="body1">
            币
          </Typography>
        </Box>
        <Box width='15%' p={1} className={classes.card}>
          <Typography variant="body1">
            最大回撤
          </Typography>
          <Typography variant="body1">
            美元
          </Typography>
          <Typography variant="body1">
            币
          </Typography>
        </Box>
        <Box width='15%' p={1} className={classes.card}>
          <Typography variant="body1">
            最大回撤
          </Typography>
          <Typography variant="body1">
            美元
          </Typography>
          <Typography variant="body1">
            币
          </Typography>
        </Box>
        <Box width='15%' p={1} className={classes.card}>
          <Typography variant="body1">
            最大回撤
          </Typography>
          <Typography variant="body1">
            美元
          </Typography>
          <Typography variant="body1">
            币
          </Typography>
        </Box>
        <Box width='15%' p={1} className={classes.card}>
          <Typography variant="body1">
            最大回撤
          </Typography>
          <Typography variant="body1">
            美元
          </Typography>
          <Typography variant="body1">
            币
          </Typography>
        </Box>
      </Box>

      <Box>
      <ResponsiveReactGridLayout
        layouts={layouts}
        breakpoints={{ md: 960, xs: 480, xxs: 0 }}
        cols={{ md: 12, xs: 4, xxs: 2 }}
        draggableHandle=".dragHandle"
        onLayoutChange={onLayoutChange}
        isDraggable={matches ? false : true}
        isResizable={matches ? false : true}
      >
        {
          layouts.md.map(item => {
              if (item.i === '1')
              return (
                <Box style={{ backgroundColor: '#eee' }} key="1">
                  <LineChart series={line1} events={events} panel="1"
                    options={{
                      chart: {
                        group: 'social',
                        id: item.i
                      }
                    }}
                  />
                </Box>
              );
              if (item.i === '2')
              return (
                <Box style={{ backgroundColor: '#eee' }} key="2">
                  <LineChart series={line1} events={events} panel="2"
                    options={{
                      chart: {
                        group: 'social',
                        id: item.i
                      }
                    }}
                    type='line'
                  />
                </Box>
              );
              if (item.i === '3')
              return (
                <Box style={{ backgroundColor: '#eee' }} key="3">
                  <LineChart series={line1} events={events} panel="3"
                    options={{
                      chart: {
                        group: 'social',
                        id: item.i
                      }
                    }}
                    type='area'
                  />
                </Box>
              );
              if (item.i === '4')
              return (
                <Box style={{ backgroundColor: '#eee' }} key="4">
                  <LineChart series={line1} events={events} panel="4" type='area'/>
                </Box>
              );
              if (item.i === '5')
              return (
                <Box style={{ backgroundColor: '#eee' }} key="5">
                  <LineChart series={line2} events={events} panel="5"/>
                </Box>
              );
              if (item.i === '6')
              return (
                <Box style={{ backgroundColor: '#eee' }} key="6">
                  <HeatmapChart events={events} panel="6" series={heatmapData}/>
                </Box>
              );
              return null;
          })
        }
        </ResponsiveReactGridLayout>
      </Box>
      </Box>
    );
  }

  function saveLayoutsToLS(value) {
    if (window.localStorage) {
      window.localStorage.setItem('Layouts', JSON.stringify(value));
    }
  }

  function getLayoutsFromLS() {
    let ls = {};
    if (window.localStorage) {
      try {
        const layouts = window.localStorage.getItem('Layouts');

        ls = (layouts && JSON.parse(layouts)) || {};
      } catch (e) {}
    }
    return ls;
  }

export default Dashboard;
